import Link from 'next/link';

const About = () => {
  return (
    <div className="about-container">
      
      <section className="about-content">
        <p>
          We are a team of passionate professionals dedicated to bringing innovative solutions to the tech industry.
        </p>
        {/* Using legacyBehavior for older versions */}
        <Link href="/contact" legacyBehavior>
          <a>Contact Us</a>
        </Link>
      </section>
      
  
    </div>
  );
}

export default About;
