import Link from 'next/link';
import Image from 'next/image';
import { useRouter } from 'next/router';

const Header = () => {
  const router = useRouter();

  const navLinks = [
    { name: 'Home', path: '/' },
    { name: 'About', path: '/about' },
    { name: 'Pages', path: '/pages' },
    { name: 'Services', path: '/services' },
  ];

  return (
    <header className="flex justify-between items-center p-4 border-b border-gray-200 bg-white">
      <div className="flex items-center gap-8">
        {navLinks.map((link) => (
          <Link key={link.name} href={link.path}>
            <a
              className={`text-[18px] font-medium leading-[25.6px] font-[Inter] ${
                router.pathname === link.path ? 'text-[#04436F]' : 'text-[#222]'
              } hover:text-[#A99558]`}
            >
              {link.name}
            </a>
          </Link>
        ))}
      </div>

      <div className="flex items-center gap-4">
        <Image src="/Link - home.png" alt="PHC Logo" width={100} height={50} />
      </div>

      <div className="flex gap-4">
        <Link href="/register">
          <a className="px-4 py-2 rounded-[6px] bg-[#A99558] text-white text-center font-[Montserrat] text-[15px] font-medium leading-normal tracking-[-0.225px]">
            Register
          </a>
        </Link>
        <Link href="/login">
          <a className="px-4 py-2 rounded-[6px] border border-[#A99558] bg-white text-[#A99558] text-center font-[Montserrat] text-[15px] font-medium leading-normal tracking-[-0.225px]">
            Log in
          </a>
        </Link>
      </div>
    </header>
  );
};

export default Header;